.move {
  top: 750;
  transform: translate(0, 750px);
  animation: 1.7s ease-in swingViewport;
  display: block;
  /* -webkit-transform: translate(0, 750px); */
}

.move.faster {
  top: 750;
  transform: translate(0, 750px);
  animation: 1.5s ease-in swingViewport;
  display: block;
  /* -webkit-transform: translate(0, 750px); */
}

.move.slow {
  top: 750;
  transform: translate(0, 750px);
  animation: 1.9s ease-in swingViewport;
  display: block;
  /* -webkit-transform: translate(0, 750px); */
}

.move.slow2 {
  top: 750;
  transform: translate(0, 750px);
  animation: 2.1s ease-in swingViewport;
  display: block;
  /* -webkit-transform: translate(0, 750px); */
}

.move.slow3 {
  top: 750;
  transform: translate(0, 750px);
  animation: 2s ease-in swingViewport;
  display: block;
  /* -webkit-transform: translate(0, 750px); */
}

.hide {
  display: none;
  top: 0;
}

@keyframes swingViewport {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(1300px);
    display: none;
  }
}
